
.goods-wrapper{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: calc(100vh - 224px);
  overflow: hidden;
}
.goods-wrapper .menu{
  box-sizing: border-box;
  width: 80px;
  height: 100%;
  -ms-flex-negative: 1;
      flex-shrink: 1;
}
.goods-wrapper .content{
  -webkit-box-flex:1;
      -ms-flex:1 0;
          flex:1 0;
  /*宽度设置一个默认值，保证兼容性*/
  width: 40px;
  height: 100%;
  /*设置一个默认高度值，以使better-scroll生效*/
}
.cart{
  -webkit-box-flex: 0;
      -ms-flex: 0 1;
          flex: 0 1;
  width: 100%;
  background-color: #141d27;
  height: 46px;
}




